<template>
  <div class="movies-search">
    <div class="container">
      <div class="form">
        <input
          type="text"
          placeholder="Type movie or serie's title"
          v-model="movieName"
          @keypress.enter="getApi"
        />
        <button type="button" class="btn btn-square" @click="getApi">
          <svg
            stroke="currentColor"
            fill="currentColor"
            stroke-width="0"
            viewBox="0 0 16 16"
            height="1.5em"
            width="1.5em"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fill-rule="evenodd"
              d="M10.442 10.442a1 1 0 011.415 0l3.85 3.85a1 1 0 01-1.414 1.415l-3.85-3.85a1 1 0 010-1.415z"
              clip-rule="evenodd"
            ></path>
            <path
              fill-rule="evenodd"
              d="M6.5 12a5.5 5.5 0 100-11 5.5 5.5 0 000 11zM13 6.5a6.5 6.5 0 11-13 0 6.5 6.5 0 0113 0z"
              clip-rule="evenodd"
            ></path>
          </svg>
        </button>
      </div>
      <div class="loading mx-auto my-3" v-if="!loading"></div>
      <div class="warn" v-if="!warnMsg">{{ warnText }}</div>
      <div class="movies-got mt-5" v-if="divActive">
        <h3>Search results of ( {{ word }} )</h3>
        <div class="row mt-3 justify-content-center">
          <div
            class="col-lg-3 col-md-4 col-6 mt-4"
            v-for="movie in allMovies"
            :key="movie.id"
          >
            <div class="movie" @click="goToInfos(movie)">
              <div class="img position-relative">
                <img :src="movie.Poster" />
                <div class="type position-absolute">{{ movie.Type }}</div>
              </div>
              <div class="foot">
                <div class="year">
                  <span>{{ movie.Year }}</span>
                </div>
                <div class="title">
                  <h4>{{ movie.Title }}</h4>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "movies-section",
  data() {
    return {
      movieName: "",
      apiKey: "2c3b38a0",
      warnMsg: true,
      warnText: "",
      divActive: false,
      allMovies: "",
      loading: true,
      word: "",
    };
  },
  mounted() {
    let results = JSON.parse(localStorage.getItem("search-results"));
    if (results) {
      this.allMovies = results;
      this.divActive = true;
      this.word = localStorage.getItem("search-word");
    }
  },
  methods: {
    async getApi() {
      this.loading = false;
      await fetch(
        `https://www.omdbapi.com/?s=${this.movieName}&apikey=${this.apiKey}`
      )
        .then((data) => {
          return data.json();
        })
        .then((data) => {
          if (this.movieName.trim()) {
            this.loading = true;
            if (data.Response == "True") {
              this.allMovies = data.Search;
              this.word = this.movieName;
              localStorage.setItem("search-word", this.word);
              localStorage.setItem(
                "search-results",
                JSON.stringify(data.Search)
              );
              this.divActive = true;
              this.warnMsg = true;
              this.movieName = "";
            } else {
              this.warnMsg = false;
              this.warnText = "please type valid name";
              this.movieName = "";
              this.loading = true;
              this.allMovies = "";
              this.word = "";
            }
          } else {
            this.warnMsg = false;
            this.warnText = "please type movie's name";
            this.loading = true;
            this.allMovies = "";
            this.divActive = false;
          }
        })
        .catch((err) => {
          console.log(err);
          this.warnMsg = false;
          this.warnText = "please type movie's name";
          this.loading = true;
        });
    },
    goToInfos(movie) {
      this.$router.push(movie.Title + " " + movie.Year);
      localStorage.setItem("movie-data", JSON.stringify(movie));
    },
  },
};
</script>

